body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main {
  height: 1150px;
  width: 800px;
  background-color: white;
  box-shadow: 5px 7px 15px 5px #b9b6b6;
  margin: 20px auto;
  font-family: system-ui;
}

.top-section {
  background-color: #151b29;
  text-align: center;
  padding: 20px;
}

.profile {
  border-radius: 50%;
  width: 150px;
}

.p1 {
  color: white;
  font-size: 40px;
  font-weight: bold;
  margin: 0px;
  margin-top: 10px;
}

.p1 span {
  font-weight: 100;
  color: #c7c7c7;
}

.p2 {
  font-size: 20px;
  color: #c7c7c7;
  margin: 0px;
  margin-top: 10px;
}

.col-div-4 {
  width: 35%;
  float: left;
}

.head {
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 600;
}

.content-box {
  padding-left: 40px;
  padding: 20px;
}

.col-div-8 {
  width: 62%;
  float: left;
}

.line {
  border-left: 1px solid #c7c7c7;
  height: 800px;
  width: 2%;
  margin-top: 30px;
  float: left;
}

.p3 {
  color: #969292;
  margin-bottom: -5px;
}

.skills {
  margin-left: -20px;
  margin-bottom: 0px;
}

.skills li {
  padding: 5px;
}

.skills li span {
  color: #969292;
}

.p4 {
  font-size: 14px;
}

.responsibilities li {
  font-size: 12px;
}

.p6 {
  font-size: 14px;
  margin-left: 10px;
}
